<template>
  <div class="content-login">
    <div class="container">
      <img class="logo" :src="imgSrc">
      <input type="text" v-model="userInfo.hotelCode" class="input hotelCode" placeholder="请输入酒店代码">
      <input type="text" v-model="userInfo.userName" class="input unseName" placeholder="请输入用户名">
      <input
        type="password"
        v-model="userInfo.password"
        class="input password"
        password="true"
        placeholder="请输入密码"
      >
      <div class="logo-btn" @click="loginFun()">登录</div>
      <div class="content-set">
        <div @click="RememberPWD()">
          <div class="checkbox">
            <i class="iconfont icon-chenggong1" v-if="checkboxIcon"></i>
          </div>记住账号
        </div>
        <router-link class="link-set" to="/mobileOrdering/networkTest">连接设置</router-link>
      </div>
    </div>
    <!-- <div class="bg" :style="{backgroundImage:'url(' + bgimgSrc+ ')'}"></div> -->
    <img class="bg" :src="bgimgSrc" alt>
  </div>
</template>

<script>
import ajaxGet from "@/conf/ajaxGet";
import { Toast, MessageBox } from "mint-ui";
import { mapState, mapGetters } from "vuex";
export default {
  data() {
    return {
      checkboxIcon: false,
      userInfo: {
        hotelCode: "",
        userName: "",
        password: ""
      },
      imgSrc: "./static/images/mobileOrdering/logo2x.png",
      bgimgSrc: "./static/images/mobileOrdering/login_BG2x.png"
    };
  },
  computed: {
    ...mapState({
      userInfos: state => state.mobileOrdering.userInfo
    })
  },
  mounted() {
    // console.log("typeof:", this.userInfos);
    // console.log("typeof:", typeof this.userInfos);
    if (typeof this.userInfos != "string" && this.userInfos.rememberPWD) {
      this.userInfo = this.userInfos;
      this.checkboxIcon = this.userInfos.rememberPWD;
    }
  },
  methods: {
    RememberPWD() {
      this.checkboxIcon = !this.checkboxIcon;
    },
    async loginFun() {
      if (
        this.userInfo.hotelCode &&
        this.userInfo.userName &&
        this.userInfo.password
      ) {
        let magicNo;
        if (window.plus) {
          magicNo = plus.device.uuid;
        } else {
          magicNo = 1234567899;
        }
        // alert(magicNo);
        //console.log("magicNo:", magicNo);
        let data = {
          params: {
            userName: this.userInfo.userName,
            password: this.userInfo.password,
            hotelCode: this.userInfo.hotelCode,
            magicNo: magicNo
          }
        };
        //console.log("data:", data);
        let res = await ajaxGet("/pos/router?method=loginApp", data);
        if (res && res.resultCode == 0) {
          let dcgateway = localStorage.getItem("dcgateway")
            ? JSON.parse(localStorage.getItem("dcgateway"))
            : {};
          let rememberPWD = false;

          if (this.checkboxIcon) {
            rememberPWD = true;
          } else {
            rememberPWD = false;
          }
          dcgateway.userInfo = {
            userName: this.userInfo.userName,
            magicNo: magicNo,
            rememberPWD: rememberPWD,
            ...res.data
          };

          localStorage.setItem("dcgateway", JSON.stringify(dcgateway));
          this.$store.commit("submitUserInfo", {
            userName: this.userInfo.userName,
            magicNo: magicNo,
            rememberPWD: rememberPWD,
            ...res.data
          });

          if (res.data.workStationCode) {
            this.$router.push("/mobileOrdering/selectClass");
          } else {
            this.$router.push("/mobileOrdering/siteInfo");
          }
        }
      } else {
        this.msg("酒店代码/用户名/密码不能为空！");
      }
    },
    // async getDishInfo(dcgateway) {
    //   let res = await ajaxGet("/pos/router?method=getCachePluDatas", {});
    //   if (res && res.resultCode == 0) {
    //     dcgateway.DishInfo = res.data;
    //     localStorage.setItem("dcgateway", JSON.stringify(dcgateway));
    //     this.$router.push("/mobileOrdering/selectClass");
    //   }
    // },
    msg(msg) {
      Toast({
        message: msg,
        position: "middle",
        duration: 3000
      });
    }
  },
  watch: {
    userInfo: {
      handler(val) {
        val.hotelCode = val.hotelCode.toUpperCase().trim();
        val.userName = val.userName.toUpperCase().trim();
      },
      deep: true
    }
  }
};
</script>

<style lang="less">
.content-login {
  width: 100%;
  .container {
    // position: relative;
    // height: 100vh;
    // overflow-y: auto;
    padding: 130px 50px 0;
    .logo {
      display: block;
      width: 197px;
      height: 58px;
      margin: 0 auto;
      margin-bottom: 35px;
    }
    .input {
      width: 100%;
      height: 45px;
      border: 1px solid #f4f4f4;
      border-radius: 100px;
      margin-bottom: 30px;
      text-align: center;
      color: #fff;
      background: none;
      outline: none;
      font-size: 16px;
    }
    .logo-btn {
      width: 100%;
      height: 45px;
      line-height: 45px;
      background-image: linear-gradient(90deg, #d3c18d 0%, #f3bb13 100%);
      background: #c6af6c;
      border-radius: 100px;
      font-size: 16px;
      color: #ffffff;
      margin-bottom: 30px;
    }
    .content-set {
      display: flex;
      justify-content: space-between;
      align-items: center;
      font-size: 12px;
      color: #ffffff;
      text-align: center;
      .checkbox {
        width: 18px;
        height: 18px;
        display: inline-block;
        background-color: #fff;
        border-radius: 5px;
        vertical-align: middle;
        margin-right: 8px;
        .iconfont {
          font-weight: bold;
          color: #f89635;
          line-height: 20px;
        }
      }
      .link-set {
        font-size: 12px;
        color: #ffffff;
      }
    }
  }
  .bg {
    position: absolute;
    top: 0px;
    left: 0px;
    width: 100%;
    min-height: 100%;
    z-index: -1;
  }
}
</style>


